import React, {
  useState,
  useEffect,
  useCallback,
  useRef,
  Suspense,
} from 'react';
import { Link, Outlet, useLocation, useParams } from 'react-router-dom';
import { apiMovies } from '../../api/api';
import { Image, Descriptions, Button } from 'antd';
import css from './MovieDetails.module.css';
import Stars from 'components/Stars/Stars';
import ScrollToTop from 'components/ScrollToTop/ScrollToTop';
import NotFound from 'pages/NotFound/NotFound';
import Spiner from 'components/Spiner/Spiner';

const MovieDetails = () => {
  const [movie, setMovies] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const { movieId } = useParams();
  const location = useLocation();
  const backLocation = useRef(location.state?.from ?? '/');

  const getMovieDetails = useCallback(async () => {
    setIsLoading(true);
    setError(null);
    try {
      if (!movieId) {
        return;
      }

      const data = await apiMovies('movie/', movieId);
      setMovies(data);
    } catch (error) {
      console.log(error);
      setError(error);
    } finally {
      setIsLoading(false);
    }
  }, [movieId]);

  useEffect(() => {
    getMovieDetails();
  }, [getMovieDetails]);

  const {
    poster_path = '',
    title = '',
    name = '',
    overview = '',
    genres = '',
    release_date = '',
    status = '',
    runtime = '',
    vote_average = 0,
  } = movie;

  if (error) {
    return <NotFound />;
  }

  return (
    <>
      {isLoading ? (
        <Spiner />
      ) : (
        <div className={css.wrapper}>
          {poster_path ? (
            <Image
              title={'Image' + title}
              className={css.movieCol}
              src={`https://image.tmdb.org/t/p/w500/${poster_path}`}
            />
          ) : (
            <Image
              title={"Image 'No poster'"}
              loading="lazy"
              width={320}
              src="error"
              fallback="data:image/png;base64,"
            />
          )}

          <Descriptions
            className={css.table}
            title=<div className={css.wrapperTitle}>
              <Link to={backLocation.current}>
                <Button className={css.btn} type="primary">
                  Back
                </Button>
              </Link>
              <h2 className={css.title}>More details</h2>
            </div>
            bordered
            column={1}
          >
            <Descriptions.Item label="Title">{title || name}</Descriptions.Item>
            <Descriptions.Item label="Rating">
              <Stars
                rating={vote_average}
                secondaryColor={'grey'}
                numberRating={vote_average}
                style={css.stars}
              />
            </Descriptions.Item>
            <Descriptions.Item label="Data release">
              {release_date}
            </Descriptions.Item>
            <Descriptions.Item label="Genres">
              {genres.length > 0 &&
                genres.map(genre => <li key={genre.id}>{genre.name}</li>)}
            </Descriptions.Item>
            <Descriptions.Item label="Status">{status}</Descriptions.Item>
            <Descriptions.Item label="Runtime">{runtime} min</Descriptions.Item>
            <Descriptions.Item label="Overview">{overview}</Descriptions.Item>
            <Descriptions.Item>
              <div className={css.button}>
                <Link to="cast">
                  <Button className={css.btn} type="primary">
                    Cast
                  </Button>
                </Link>
                <Link to="reviews">
                  <Button className={css.btn} type="primary">
                    Reviews
                  </Button>
                </Link>
              </div>
            </Descriptions.Item>
          </Descriptions>
        </div>
      )}
      <Suspense fallback={<Spiner />}>
        <Outlet />
      </Suspense>
      <ScrollToTop />
    </>
  );
};

export default MovieDetails;
